/* src/styles/colors.css */

:root {
  /* ----------------- Backgrounds ----------------- */
  --bg-app: #0F0C28;          /* main app background */
  --bg-nav: #1A133E;          /* top navigation bar */
  --bg-card: #23184B;         /* card backgrounds / tables */
  --bg-card-alt: #1C1040;     /* alternate card / bench rows */
  --bg-highlight: rgba(255, 140, 66, 0.1); /* highlight backgrounds for rank 1 */
  --bg-rank-1: rgba(255, 215, 0, 0.10);     /* gold 10% */
  --bg-rank-2: rgba(192, 192, 192, 0.10);   /* silver 10% */
  --bg-rank-3: rgba(255, 140, 66, 0.10); 

  /* ----------------- Text ----------------- */
  --text-primary: #E0E0E0;      /* main text */
  --text-secondary: #B0B0B0;    /* metadata / secondary text */
  --text-disabled: #7A7A90;     /* muted / disabled */
  --text-active-tab: #FF8C42;   /* active tab / highlighted text */
  --text-rank-1: #FFD700;       /* gold / 1st place */
  --text-rank-2: #C0C0C0;       /* silver / 2nd place */
  --text-rank-3: #FF8C42;       /* bronze / 3rd place */
  --text-rank-default: #9CA3AF; /* default gray */
  --text-success: #33D9B2;      /* positive stats / gains */
  --text-error: #FF6E6E;        /* negative stats / losses */
  --text-warning: #FFD966;      /* warnings / ties */
  --text-accent: #A66FFF;       /* secondary accents */
  --text-muted: #888888;        /* for bench / disabled lines */
  --text-cyan: #22D3EE;         /* cyan stats or headings */
  --text-amber: #FACC15;        /* amber highlights */

  /* ----------------- Borders / Dividers ----------------- */
  --border-primary: #4B5563;   /* default card / table border */
  --border-rank-1: #FFD700;
  --border-rank-2: #C0C0C0;
  --border-rank-3: #FF8C42;
  --border-divider: #7A7A90;

  /* ----------------- Accents / Actions ----------------- */
  --accent-primary: #FF8C42;    /* fantasy points, primary call-to-action */
  --accent-secondary: #A66FFF;  /* hover effects, selected cards, secondary highlights */
  --accent-success: #33D9B2;    /* positive points / gains */
  --accent-error: #FF6E6E;      /* negative points / alerts */
  --hover-bg: #341A63;           /* hover for cards, buttons, table rows */
  --active-bg: #2B1460;          /* active tab or selected card */
  --shadow-hover: rgba(166, 111, 255, 0.3); /* subtle glow / shadow */
  
  /* ----------------- Specific Utility Colors ----------------- */
  --bg-bench: rgba(0, 0, 0, 0.2);
  --bg-missing-slot: rgba(139, 0, 0, 0.25); /* missing lineup rows */
}
